.app{min-height:100vh;background-color:#FFF;background-position:center center}

.employees-mosaic{display:inline-block;text-align:center;line-height:0;max-width:1100px}@media (max-width: 700px){.employees-mosaic{max-width:730px}}.employees-mosaic__employee{display:inline-block;margin:30px 10px;position:relative;line-height:normal;width:200px;vertical-align:top}@media (max-width: 700px){.employees-mosaic__employee{width:140px;margin:30px 3px}}.employees-mosaic__employee__profile{display:inline-block;position:relative;border-radius:50%;overflow:hidden;width:150px;height:150px;border:none;cursor:pointer;box-shadow:0 0 0 0 rgba(69,46,118,0.5);transition:box-shadow 100ms ease-in-out}.employees-mosaic__employee__profile:focus{outline:none;box-shadow:0 0 1px 2px rgba(69,46,118,0.5)}.employees-mosaic__employee__profile:focus .employees-mosaic__employee__profile__image--filter{opacity:0.1}.employees-mosaic__employee__profile:hover,.employees-mosaic__employee__profile:focus{outline:none;box-shadow:0 0 0.5px 2px rgba(69,46,118,0.5)}.employees-mosaic__employee__profile:hover .employees-mosaic__employee__profile__image--filter,.employees-mosaic__employee__profile:focus .employees-mosaic__employee__profile__image--filter{opacity:0.1}@media (max-width: 700px){.employees-mosaic__employee__profile{width:105px;height:105px}}.employees-mosaic__employee__profile__image,.employees-mosaic__employee__profile__image--filter{display:inline-block;position:absolute;left:-5%;top:-5%;width:110%;height:110%;background-position:center center;background-size:cover;background-clip:content-box;background-color:#FFF;box-sizing:border-box;pointer-events:none}.employees-mosaic__employee__profile__image--filter{opacity:0.99;transition:opacity 100ms ease-in-out}.employees-mosaic__employee__mail{position:absolute;top:8.88535px;left:calc(50% + 32.7707px);border-radius:50%;background-color:#452e76;color:#FFF;text-decoration:none;width:30px;height:30px;line-height:30px;text-align:center;transition:transform 150ms ease-in-out, box-shadow 150ms ease-in-out;padding:5px;box-sizing:border-box;font-size:0;box-shadow:0 0 0 0 rgba(69,46,118,0.5)}.employees-mosaic__employee__mail:hover,.employees-mosaic__employee__mail:focus{outline:none;transform:scale(1.2);box-shadow:1px 1px 2px 0px rgba(0,0,0,0.7)}.employees-mosaic__employee__mail:hover .employees-mosaic__employee__mail__top,.employees-mosaic__employee__mail:focus .employees-mosaic__employee__mail__top{transform:rotateX(270deg);transition-delay:150ms}.employees-mosaic__employee__mail:hover .employees-mosaic__employee__mail__back,.employees-mosaic__employee__mail:focus .employees-mosaic__employee__mail__back{transform:translateY(-10px);transition-delay:330ms}.employees-mosaic__employee__mail svg{width:80%;margin-top:-3px}.employees-mosaic__employee__mail svg path{fill:#FFF;stroke-width:none}.employees-mosaic__employee__mail svg .employees-mosaic__employee__mail__top{transform:rotateX(0deg);transform-origin:50% 40px;transition:transform 330ms ease-in-out;transition-delay:0ms}.employees-mosaic__employee__mail svg .employees-mosaic__employee__mail__back{fill:rgba(255,255,255,0.5);transition:transform 150ms ease-in-out}#employee-filter-svg{display:none}.employees-mosaic__employee__name{color:#452e76;margin:10px 0;font-weight:400}.employees-mosaic__employee__position{color:#48c3dc;font-weight:300;font-size:0.9rem;margin-bottom:5px}.employees-mosaic__employee__location{color:#000;font-weight:300;font-size:0.9rem}

.homepage{text-align:center;min-height:100vh;line-height:0}.homepage::before{content:"";display:inline-block;width:0;height:100vh;vertical-align:middle}.homepage_wrapper{display:inline-block;vertical-align:middle;line-height:normal;line-height:initial}.homepage__header{color:#452e76;font-size:2.9rem;padding:0;margin:10px  0 50px 0;font-weight:600}

.slider-wrapper{overflow:hidden;position:fixed;top:0;left:0;right:0;bottom:0;pointer-events:none}.slider-wrapper-appear .slider,.slider-wrapper-enter .slider{transform:translateY(100vh)}.slider-wrapper-appear-active .slider,.slider-wrapper-enter-active .slider{transform:translateY(0vh);transition:transform 200ms ease-out}.slider-wrapper-appear-done .slider,.slider-wrapper-enter-done .slider{transform:translateY(0vh)}.slider-wrapper-exit .slider{transform:translateY(0vh)}.slider-wrapper-exit-active .slider{transform:translateY(100vh);transition:transform 200ms ease-in}.slider-wrapper-exit-done .slider{transform:translateY(100vh)}.slider{background-color:#FFF;width:100%;height:100%;transform-origin:center center;transform:translateY(100vh);pointer-events:all}.slider__slide{background-size:cover;background-position:center center;line-height:0}.slider__slide:before{content:"";position:absolute;top:0;left:0;right:0;bottom:0;background:#48c3dc;background:linear-gradient(0deg, #24626e 0%, #48c3dc 100%);opacity:0.55}.slider__slide:after{content:"";display:inline-block;width:0;height:100%;vertical-align:middle}.slider__slide__data{display:inline-block;vertical-align:middle;color:#000;position:relative;line-height:normal;line-height:initial;border:3px dashed rgba(255,255,255,0.25);padding:30px;color:#FFF;text-shadow:0 2px 2px rgba(0,0,0,0.55);border-image-source:url("/images/border_slider.png");border-image-width:3px;border-image-slice:11;border-image-repeat:round;margin:0 50px}@media (max-width: 768px){.slider__slide__data{padding:10px}}.slider__slide__data__header{display:block;font-size:40px;font-weight:300}@media (max-width: 500px){.slider__slide__data__header{font-size:30px}}@media (max-width: 400px){.slider__slide__data__header{font-size:20px}}.slider__slide__data__content{display:block;font-size:60px;font-weight:600}@media (max-width: 500px){.slider__slide__data__content{font-size:50px}}@media (max-width: 400px){.slider__slide__data__content{font-size:40px}}@media (max-width: 350px){.slider__slide__data__content{font-size:25px}}.slider__button,.slider__button--slide,.slider__next,.slider__prev,.slider__exit{position:absolute;z-index:1;border:none;background-color:unset;padding:0;margin:0;cursor:pointer}.slider__button svg,.slider__button--slide svg,.slider__next svg,.slider__prev svg,.slider__exit svg{fill:#FFF;width:30px;transition:fill 150ms ease}.slider__button:hover,.slider__button--slide:hover,.slider__next:hover,.slider__prev:hover,.slider__exit:hover,.slider__button:focus,.slider__button--slide:focus,.slider__next:focus,.slider__prev:focus,.slider__exit:focus{outline:none}.slider__button:hover svg,.slider__button--slide:hover svg,.slider__next:hover svg,.slider__prev:hover svg,.slider__exit:hover svg,.slider__button:focus svg,.slider__button--slide:focus svg,.slider__next:focus svg,.slider__prev:focus svg,.slider__exit:focus svg{fill:#e6e6e6}.slider__button--slide,.slider__next,.slider__prev{height:50px;top:calc(50% - 25px)}.slider__next{right:10px}.slider__prev{left:10px}.slider__exit{top:0px;right:0px;width:50px;height:50px}

